 /* reset css */
body {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Noto Sans TC','Montserrat', sans-serif;
  margin: 0px;
  padding: 0px;
}
ul,
ol,
li {
  font-family: 'Noto Sans TC','Montserrat', sans-serif;
  list-style: none;
  margin: 0px;
  padding: 0px;
}
a,
a:hover,
a:focus {
  font-family: 'Noto Sans TC','Montserrat', sans-serif;
  text-decoration: none;
}
img {
  width: 100%;
  height: 100%;
  display: block;
}
.btn,
button,
button:focus,
input:focus,
textarea:focus,
select,
select:focus,
a:focus {
  outline: 0px !important;
}
button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
  outline: 0;
}
button[disabled] {
  pointer-events: none;
}
input {
  padding: 0;
}
input:required {
  box-shadow: none;
}
input:invalid {
  box-shadow: none;
}

span {
  font-family: 'Noto Sans TC','Montserrat', sans-serif;
}
// basic setting
//color
$body-color:#ff6600;
$primary: #0062ff;
$secondary: #6c757d;
$success: #3dd589;
$warning:#ffc542;
$error:#fc5a5a;
$sup-orange: #ff974a;
$sup-blue: #50b5ff;
$sup-green: #82c43c;
$sup-purple: #a461d8;
$sup-pink: #ff9ad5;
$text-color: #171725;
$input-bg: #f5f8fa; 

//container border
%container-border {
  border: 1px solid #e6ecf0;
}
//avatar-min basic
%avatar-min {
  height: 50px;
  width: 50px;
  img {
    border-radius: 50%;
  }
}
//共用按鈕樣式 寬度要另外給
%main-button {
  height: 40px;
  padding: 8px;
  border: 1px solid $body-color;
  border-radius: 50px;
  line-height: 24px;
  color: $body-color;
}

//tabs元件共用樣式
%tabs-style {
  @extend %container-border;
  height: 52px;
  width: 100%;
  border-top: 0px;
  margin-top: 10px;
  display: flex;
  .tab {
    width: 130px;
    text-align: center;
    line-height: 52px;
    font-weight: 700;
    font-size: 15px;
    color: #657786;
    cursor: pointer;
  }
  .activeTab {
    color: $body-color;
    border-bottom: 2px solid $body-color;
  }
}

//共用Input樣式 照AuthInput結構使用 放入className {.text_container}
//Margin要自己給
%input-style {
  background-color: $input-bg;
  width: 100%;
  height: 54px;
  padding: 0 16px;
  line-height: 22px;
  border-bottom: 2px solid #657786;
  .label {
    font-size: 14px;
    color: #696974;
    text-align: start;
  }
  .input {
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    border: 0;
    background-color: $input-bg;
    font-size: 16px;
    line-height: 26px;
  }
}

//font
h1 {
  font-weight: bold;
  font-size: 68px;
}

h2 {
  font-weight: bold;
  font-size: 42px;
}

h3 {
  font-weight: 500;
  font-size: 28px;
}

h4 {
  font-weight: 500;
  font-size: 24px;
}

h5 {
  font-weight: 500;
  font-size: 18px;
}